<template>
  <div style="display: flex">
    <div class="left">
      <img src="../../../../assets/logo.png" alt="" class="header">
    </div>
    <div class="right">
      <div class="item" style="margin-top: 60px;">
        <p>用户名：</p>
        <input type="text" v-model="input" class="input">
        <img src="../../../../images/basic1.png" alt="">
      </div>
      <div class="item" style="margin-top: 24px;">
        <p>手机号：</p>
        <input type="text" v-model="input" class="input">
        <img src="../../../../images/basic1.png" alt="">
      </div>
      <div class="item" style="margin-top: 56px;">
        <p>邮箱：</p>
        <h4 v-if="!state" style="font-size: 14px;color: #F75F40;">未认证</h4>
        <h4 v-if="state" style="font-size: 14px;color:  #00488B;">已认证</h4>
      </div>
      <div class="item" style="margin-top: 24px;">
        <p>微信号：</p>
        <h4 v-if="!state" style="font-size: 14px;color: #F75F40;">未认证</h4>
        <h4 v-if="state" style="font-size: 14px;color:  #00488B;">已认证</h4>
      </div>
      <div class="item" style="margin-top: 24px;">
        <p>身份认证：</p>
        <h4 v-if="!state" style="font-size: 14px;color: #F75F40;">未认证</h4>
        <h4 v-if="state" style="font-size: 14px;color:  #00488B;">已认证</h4>
      </div>
      <div class="item" style="margin-top: 24px;">
        <p style="margin-right: 20px;">认证状态：</p>
        <img src="../../../../images/basic2.png" alt="">
        <img src="../../../../images/basic3.png" alt="">
        <img src="../../../../images/basic4.png" alt="">
      </div>
      <div class="item" style="margin-top: 56px;">
        <el-input
          style="width: 660px;"
          type="textarea"
          :rows="8"
          placeholder="请输入内容"
          v-model="input">
        </el-input>
      </div>
      <div class="btn" style="margin-top: 45px;">
        保存
      </div>
    </div>
  </div>
</template>

<script>
  import { mapState } from 'vuex'

  export default {
    data () {
      return {
        input: '234',
        state: true
      }
    },
    computed: {
      ...mapState({
        // isCollapse: state => state.isCollapse
      })
    },
    methods: {
    }
  }
</script>

<style scoped>
  .left{
    width: 200px;
    display: flex;
    justify-content: center;
  }
  .header{
    width: 80px;height: 80px;border-radius: 50%;border: 1px solid red;
    margin-top: 60px;
  }
  .item{
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .item p{
    ont-size: 16px;
    color: #333333;
    width: 80px;
    text-align: left;
  }
  .input{
    width: 120px;
    height: 25px;
    font-size:16px;
    color: #333;
    margin-left: 10px;
  }
  .item img{
    width: 20px;
    height: 20px;
  }
  .item h4{
    margin-left: 25px;
  }
  .btn{
    width: 240px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    background: #00488B;
    border-radius: 4px;
    font-size: 18px;
    color: #FFFFFF;
  }
</style>



