<template>
  <div>
    <div class="container">
      <div class="left">
        <el-menu
          default-active="1"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose">
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-location"></i>
              <span>个人资料</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="1-1" @click="$router.push('/basic')">基本资料</el-menu-item>
              <el-menu-item index="1-2" @click="$router.push('/identity')">身份认证</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-menu-item index="2" @click="$router.push('/report')">
            <i class="el-icon-menu"></i>
            <span slot="title">我的举报</span>
          </el-menu-item>
          <el-menu-item index="3" @click="$router.push('/message')">
            <i class="el-icon-setting"></i>
            <span slot="title">我的消息</span>
          </el-menu-item>
        </el-menu>
      </div>
      <div class="right">
        <transition name="router-fade" mode="out-in">
          <keep-alive>
            <router-view></router-view>
          </keep-alive>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {
    }
  },
  computed: {
    ...mapState({
      // isCollapse: state => state.isCollapse
    })
  },
  methods: {
    methods: {
    }
  }
}
</script>

<style scoped>
  .container{
    width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    padding-top: 30px;
    padding-bottom: 50px;
  }
  .left{
    width: 180px;
    height: 975px;
    background-color: #fff;
  }
  .right{
    width: 1000px;
    background-color: #fff;
  }
  .el-menu-vertical-demo, .el-menu-item{
    background-color: #fff;
    overflow: hidden;
  }
</style>



