import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index/index'
import home from '@/pages/home/index'
import miss from '@/pages/404/index'
import disclose from '@/pages/disclose/index'
import sign from '@/pages/signin/index'
import allege from '@/pages/allege/index'
import personal from '@/pages/personal/index'
import basic from '@/pages/personal/children/basic/index'
import identity from '@/pages/personal/children/identity/index'
import report from '@/pages/personal/children/report/index'
import message from '@/pages/personal/children/message/index'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: index,
      children: [
        { path: '', component: home },
        { path: '/disclose', component: disclose },
        { path: '/sign', component: sign },
        { path: '/allege', component: allege },
        {
          path: '/personal',
          component: personal,
          children: [
            { path: '', redirect: '/basic' },
            { path: '/basic', component: basic },
            { path: '/identity', component: identity },
            { path: '/report', component: report },
            { path: '/message', component: message }
          ]
        }
      ]
    },
    { path: '*', component: miss }
  ]
})



