<template>
  <div>
    <h1>身份验证</h1>
    <p class="line"></p>
    <div class="list">
      <div class="item">
        <h2>国籍</h2>
        <el-cascader
          placeholder="请选择国家"
          style="width: 460px;height: 50px;"
          v-model="country_id"
          :options="language?countriesC:countriesE"
          :show-all-levels="false">
        </el-cascader>
      </div>
      <div class="item">
        <h2>证件类型</h2>
        <el-form :inline="true">
          <el-form-item>
            <el-select v-model="type" placeholder="请选择证件类型" style="width: 460px;height: 50px">
              <el-option label="身份证" value="身份证"></el-option>
              <el-option label="护照" value="护照"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <div class="item">
        <h2>证件姓名</h2>
        <el-form>
          <el-form-item>
            <el-input style="width: 460px;height: 50px" v-model="name"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div class="item">
        <h2>证件号码</h2>
        <el-form>
          <el-form-item>
            <el-input style="width: 460px;height: 50px" v-model="id_no"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div class="item">
        <h2>有效期</h2>
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item label="">
            <el-input style="width: 214px;height: 50px" v-model="validate_begin_at"></el-input>
            <span>至</span>
            <el-input style="width: 214px;height: 50px" v-model="validate_end_at"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div class="item" style="align-items: flex-start">
        <h2>正面身份证</h2>
        <el-upload
          action="http://otc_dev.365yf.cc/images/upload"
          :on-success='handleSuccess1'
          name="image"
          drag
          :show-file-list="false"
          :limit="1">
          <img v-if="front_image" :src="front_image" class="avatar">
          <div v-else>
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          </div>
        </el-upload>
      </div>
      <div class="item" style="align-items: flex-start">
        <h2>反面身份证</h2>
        <el-upload
          action="http://otc_dev.365yf.cc/images/upload"
          :on-success='handleSuccess2'
          name="image"
          drag
          :show-file-list="false"
          :limit="1">
          <img v-if="back_image" :src="back_image" class="avatar">
          <div v-else>
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          </div>
        </el-upload>
      </div>
      <div class="item" style="align-items: flex-start">
        <h2>手持身份证</h2>
        <el-upload
          action="http://otc_dev.365yf.cc/images/upload"
          :on-success='handleSuccess3'
          name="image"
          drag
          :show-file-list="false"
          :limit="1">
          <img v-if="hold_image" :src="hold_image" class="avatar">
          <div v-else>
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          </div>
        </el-upload>
      </div>
      <p class="submit" @click="postIdCards">以上信息无误，申请认证</p>
    </div>
    <div class="list2" v-if="false">
      <div class="list2-header" v-if="false">
        <img src="../../.././../images/personal1.png" alt="" style="width: 169px;height: 135px;">
        <p style="color: #333333;">正在审核中...</p>
      </div>
      <div class="list2-header" v-if="false">
        <img src="../../.././../images/personal2.png" alt="">
        <p style="color: #04AB11;">审核已通过</p>
      </div>
      <div class="list2-header">
        <img src="../../.././../images/personal3.png" alt="">
        <p style="color: #E03B3B;">审核失败，证件信息不正确</p>
      </div>
      <div class="item2">
        <h2>国籍</h2>
        <p>中国</p>
      </div>
      <div class="item2">
        <h2>证件类型</h2>
        <p>中国</p>
      </div>
      <div class="item2">
        <h2>证件姓名</h2>
        <p>中国</p>
      </div>
      <div class="item2">
        <h2>证件号码</h2>
        <p>中国</p>
      </div>
      <div class="item2">
        <h2>有效期限</h2>
        <p>中国</p>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  import { httpGet, httpPost } from '../../../../utils/index'

  export default {
    data () {
      return {
        country_id: [],
        type: '',
        name: '',
        id_no: '',
        validate_begin_at: '',
        validate_end_at: '',
        front_image: '',
        back_image: '',
        hold_image: ''
      }
    },
    computed: {
      ...mapState(['language', 'countriesC', 'countriesE', 'userInfo'])
    },
    methods: {
      postIdCards () {
        const that = this
        httpPost('/id_cards/create', {
          sid: that.userInfo.sid,
          country_id: that.country_id[1],
          type: that.type,
          name: that.name,
          id_no: that.id_no,
          validate_begin_at: that.validate_begin_at,
          validate_end_at: that.validate_end_at,
          front_image: that.front_image,
          back_image: that.back_image,
          hold_image: that.hold_image
        }, function (res) {
          console.log(res)
        })
      },
      handleSuccess1 (file) {
        this.front_image = file.image_url
      },
      handleSuccess2 (file) {
        this.back_image = file.image_url
      },
      handleSuccess3 (file) {
        this.hold_image = file.image_url
      }
    }
  }
</script>

<style scoped>
  h1{
    font-family: PingFangSC-Regular;
    font-size: 20px;
    color: #333333;
    letter-spacing: -0.34px;
    width: 100px;
    border-bottom: 2px solid #314E8C;
    padding-bottom: 10px;
    margin-left: 50px;
    padding-top: 40px;
    text-align: center;
  }
  .line{
    width: 920px;
    height: 2px;
    background-color: #EFF3F5;
    margin: 0 auto;
  }
  .item{
    margin-top:50px;
    display: flex;
    align-items: center;
  }
  .item>h2{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #333333;
    letter-spacing: -0.24px;
    width: 140px;
    height: 50px;
    padding-left: 60px;
  }
  .item>div{
    width: 460px;
  }
  .submit{
    font-family: PingFangSC-Regular;
    font-size: 20px;
    color: #FFFFFF;
    letter-spacing: -0.34px;
    width: 280px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    margin-left: 280px;
    margin-top: 98px;
    margin-bottom: 78px;
    background-color: #314E8C;
  }
  .list2{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .list2-header{
    margin-bottom: 55px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .list2-header img{
    width:120px;
    height: 120px;
    margin-top: 65px;
  }
  .list2-header p{
    font-size: 24px;
    letter-spacing: -0.41px;
    margin-top: 30px;
  }
  .item2{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
  }
  .item2 h2{
    font-size: 16px;
    color: #666666;
    letter-spacing: -0.27px;
    width: 100px;

  }
  .item2 p{
    font-size: 16px;
    color: #333333;
    letter-spacing: -0.27px;
    width: 428px;
    height: 50px;
    background-color: #F5F5F5;
    line-height: 50px;
    padding-left: 30px;
  }
  .avatar{
    width: 360px;
    height: 180px;
  }
</style>



