<template>
  <div class="home">
    <div class="left">
      <h1>场外交易黑名单查询系统</h1>
      <div class="subhead">
        <img src="../../images/safety.png" alt="">
        <p>维护场外坏境从你他做起</p>
      </div>
      <div class="search">
        <input type="text" placeholder="请输入要查询的手机号/QQ号/微信号/身份证号/姓名" v-model="content">
        <p @click="blacksSearch">查询</p>
      </div>
      <div class="null" v-if="!showBlack && show">
        <img src="../../images/null.png" alt="">
        <span>未查询到信息～</span>
        <p @click="$router.push('/disclose')">我要举报</p>
      </div>
      <div class="details" v-if="showBlack && show">
        <div class="hint">
          <p>已被<span>1</span>人举报</p>
          <div @click="$router.push('/disclose')">我要举报Ta</div>
        </div>
        <div class="item" style="margin-top: 64px;">
          <h3>微信号：</h3>
          <p>{{black.qq_no}}</p>
        </div>
        <div class="item">
          <h3>QQ号：</h3>
          <p>{{black.wx_no}}</p>
        </div>
        <div class="item">
          <h3>手机号：</h3>
          <p>{{black.mobile}}</p>
        </div>
        <div class="item">
          <h3>姓名：</h3>
          <p>{{black.name}}</p>
        </div>
        <div class="item">
          <h3>证件号：</h3>
          <p>{{black.id_no}}</p>
        </div>
        <div class="item">
          <h3>被骗币种：</h3>
          <p>{{black.symbol_id}}</p>
        </div>
        <div class="item">
          <h3>骗子钱包地址：</h3>
          <p>{{black.wallet_address}}</p>
        </div>
        <div class="item" style="align-items: flex-start">
          <h3>举报内容：</h3>
          <p>{{black.black_content}}</p>
        </div>
        <div class="item" style="align-items: flex-start">
          <h3>证件信息：</h3>
          <div>
            <img :src="item" alt="" v-for="(item,index) in black.id_card_images" style="width: 260px;height: 160px;">
          </div>
        </div>
        <div class="item" style="align-items: flex-start">
          <h3>举报截图：</h3>
          <div>
            <img :src="item" alt="" v-for="(item,index) in black.images" style="width: 140px;height: 248px;">
          </div>
        </div>
        <div class="item">
          <h3></h3>
          <div class="end">
            <p>被人恶意举报？</p>
            <img src="../../images/finger.png" alt="">
            <p style="color: #F75F40;" @click="$router.push('/allege')">我要澄清</p>
          </div>
        </div>
      </div>
    </div>
    <div class="right">
      <h2>最新黑名单</h2>
      <div class="wrap">
        <div class="list" :style="{top}">
          <div class="item" v-for="item in all_blacks" v-if="all_blacks.length > 0">
            <p><span>微信号：</span>{{item.wx_no}}</p>
            <p><span>手机号：</span>{{item.mobile}}</p>
            <p><span>姓名：</span>{{item.name}}</p>
            <p><span>证件号：</span>{{item.id_no}}</p>
            <div style="display: flex;justify-content: space-between;margin-top: 30px;align-items: center">
              <p>已被<span style="font-size: 18px;color: #F75F40;">{{item.black_num}}</span>人举报</p>
              <span>查看详细>></span>
            </div>
          </div>
          <div class="item" v-for="item in all_blacks" v-if="all_blacks.length > 0">
            <p><span>微信号：</span>{{item.wx_no}}</p>
            <p><span>手机号：</span>{{item.mobile}}</p>
            <p><span>姓名：</span>{{item.name}}</p>
            <p><span>证件号：</span>{{item.id_no}}</p>
            <div style="display: flex;justify-content: space-between;margin-top: 30px;align-items: center">
              <p>已被<span style="font-size: 18px;color: #F75F40;">{{item.black_num}}</span>人举报</p>
              <span>查看详细>></span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapState, mapMutations} from 'vuex'
  import { baseUrl } from '../../utils/base'
  import { httpGet } from '../../utils/index'
  export default {
    data () {
      return {
        input: '',
        content: '',
        black: [],
        all_blacks: [],
        activeIndex: 0,
        dir: 1,
        showBlack: false,
        show: false
      }
    },
    computed: {
      ...mapState(['hasLogin']),
      top () {
        return -this.activeIndex * 217 + 'px'
      }
    },
    mounted () {
      this.getAllBlacks();
      setInterval(() => {
        if (this.dir) {
          this.activeIndex += 1
        } else {
          this.activeIndex -= 1
        }
        if (this.activeIndex === this.all_blacks.length) {
          this.dir = 0
        }
        if (this.activeIndex === 0) {
          this.dir = 1
        }
      }, 2000)
    },
    methods: {
//      ...mapMutations([''])
      blacksSearch () {
        const that = this
        if (!that.content) {
          this.$message.error('请输入搜索内容')
          return
        }
        httpGet('/blacks/search', {content: that.content}, res => {
          that.show = true;
          if (res.data.error_code === 0) {
            that.black = res.data.black
            that.showBlack = true
            that.content = ''
          } else {
            that.showBlack = false
          }
        })
      },
      getAllBlacks () {
        const that = this
        httpGet('/blacks/all_blacks', {}, res => {
          if (res.data.error_code === 0) {
            that.all_blacks = res.data.blacks
          }
        })
      }
    }
  }
</script>

<style scoped>
  .home{
    width: 1920px;
    /*background-image: url("../../images/bg.png");*/
    /*background-repeat: no-repeat;*/
    /*background-size: 100% 100%;*/
    background-color: #000;
    display: flex;
    justify-content: flex-end;
    padding-bottom: 50px;
  }
  .left{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 1200px;
  }
  .right{
    width: 320px;
    height: 736px;
    border: 1px solid #000;
    background-color: #fff;
    margin-left: 32px;
    margin-top: 100px;
    margin-right: 40px;
  }
  .left h1{
    font-size: 60px;
    color: #FFFFFF;
    margin-top: 140px;
  }
  .subhead{
    margin-top: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .subhead img{
    width: 45px;
    height: 52px;
  }
  .subhead p{
    font-size: 40px;
    background: linear-gradient(180deg, #ECCD6C 0%, #ECBF3C 31%, #BD921D 73%, #EEBF4C 100%);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    margin-left: 25px;
  }
  .search{
    width: 828px;
    height: 80px;
    background: #FFFFFF;
    border: 1px solid #979797;
    border-radius: 8px;
    display: flex;
    overflow: hidden;
    margin-top: 95px;
  }
  .search input{
    width: 648px;
    height: 80px;
    font-size: 20px;
    color: #999999;
    padding-left: 20px;
  }
  .search p{
    width: 160px;
    height: 80px;
    background: #00488B;
    line-height: 80px;
    text-align: center;
    font-size: 24px;
    color: #FFFFFF;
  }
  .null{
    width: 1200px;
    background-color: #fff;
    margin-top: 50px;
    padding-bottom: 65px;
    height: 530px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .null img{
    width:162px;
    height: 130px;
  }
  .null span{
    font-size: 16px;
    color: #999999;
    margin-top: 20px;
  }
  .null p{
    width: 233px;
    height: 50px;
    background: #00488B;
    border-radius: 4px;
    line-height: 50px;
    text-align: center;
    font-size: 16px;
    color: #FFFFFF;
    margin-top: 40px;
  }
  .details{
    width: 1200px;
    background-color: #fff;
    margin-top: 50px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-bottom: 65px;
  }
  .right h2{
    height: 80px;
    background: #F5F7F9;
    text-align: center;
    line-height: 80px;
    font-size: 24px;
    color: #333333;
  }
  .right .item{
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-right: 20px;
    height: 217px;
    border-bottom: 1px solid #F6F6F6;
  }
  .right .item p{
    font-size: 14px;
    color: #333333;
  }
  .right .item>p{
    margin-top: 10px;
  }
  .right .item  span{
    font-size: 14px;
    color: #666666;
  }
  .hint{
    position: absolute;
    right: 28px;
    top:60px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .hint p{
    font-size: 20px;
    color: #333333;
  }
  .hint p span{
    font-size: 40px;
    color: #F75F40;
  }
  .hint div{
    width: 140px;
    height: 40px;
    background: #00488B;
    border-radius: 4px;
    font-size: 14px;
    color: #FFFFFF;
    text-align: center;
    line-height: 40px;
    margin-left: 30px;
  }
  .details .item{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 110px;
    padding-right: 38px;
    margin-top: 24px;
  }
  .details .item h3{
    width: 112px;
    margin-right: 33px;
    text-align: left;
    font-size: 16px;
    color: #666666;
  }
  .details .item p{
    font-size: 16px;
    color: #333333;
  }
  .details .item div{
    width: 826px;
    height: auto;
    overflow: hidden;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap:wrap;
  }
  .details .item div img{
    margin-right: 10px;
  }
  .details .item .end{
    justify-content: flex-start;
    align-items: center;
  }
  .details .item .end p{
    font-size: 20px;
    color: #333333;
  }
  .details .item .end img{
    width: 34px;
    height: 26px;
    margin-left: 10px;
    margin-right: 10px;
  }
  .wrap{
    overflow:hidden;
    width: 320px;
    height: 651px;
  }
  .list{
    position: relative;
    transition: top 2s;
  }
</style>



